((w, d) => {
  window.addEventListener('DOMContentLoaded', () => {
    var swiper2 = new Swiper(".mySwiper", {
      spaceBetween: 10,
      slidesPerView: 6,
      freeMode: true,
      watchSlidesProgress: true,
    });
  var swiper = new Swiper(".serviceSwiper", {
      pagination: {
        el: ".swiper-pagination",
        type: "fraction",
      },
      effect: 'fade',
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
      thumbs: {
          swiper: swiper2,
      },
      loop: true,
      on : {
        realIndexChange: function(swiper) {
          let activeIndex = swiper.realIndex
          
          animateText(activeIndex)
        }
      }
    });
  
    function animateText(slideNum) {
      let slideNumber = document.querySelector('.count');
      slideNumber.classList.add('move');
      setTimeout(() => {
        slideNumber.innerHTML = `${slideNum + 1}`
        slideNumber.classList.remove('move');
      }, 400)
    };
  
  
    $(function () {
      window.addEventListener("scroll", function (event) {
        
        var top = this.pageYOffset;
    
        var layers = $(".parralax__img-container");
        var speed, yPos;
        layers.each((el)  => {
          speed = $(this).attr("data-speed");
          var yPos = -((top * speed) / 100);
          $(this).attr("style", "transform: translate3d(0px, " + yPos + "px, 0px)");
        });
      });
    });
  
    
    let dropdown = document.querySelectorAll('.dropdown');
    dropdown.forEach(el => el.addEventListener('click', (e) => {
      e.target.closest('.dropdown').classList.toggle('active');
    }));

    let option = document.querySelectorAll('.option');
    option.forEach(el => {
      el.querySelectorAll('div').forEach(el => {
        el.addEventListener('click', (e) => {
          // console.dir(e.target.innerHTML);
          console.log(e.target);
          e.target.closest('.option').previousElementSibling.value = e.target.innerHTML;
          // textBox.value = e.target.innerHTML
        })
      })
    })
  })
  




  const form = d.getElementById('form__zakaz');
  const ds = d.getElementById('file_display');
  const input_f = d.getElementById('myfile');
  if(!input_f) return true;

  input_f.onchange = function() {
      ds.innerHTML = '';

      if (this.files.length == 0) ds.closest('.file-display--container').classList.add('d-none');
      else ds.closest('.file-display--container').classList.remove('d-none');

      [].forEach.call(this.files, el => {
          let file_name = document.createElement('div');
          file_name.innerText = el.name;
          ds.append(file_name);
      });
  };






  const array = document.querySelectorAll('.porfolio__item-mobile');
  array.forEach(el => {
    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: el,
        start: "top 60%",
        end: 'top 20%',
        scrub: 1,
      }
    })
    tl.to(el, {y: 40, duration: 2})
  })
  

  VanillaTilt.init(document.querySelectorAll('.porfolio__item'),{
    max: 10,
    speed: 400
  })

  new WOW().init();

})(window, document)



  