(() => {
    var scrol_fix = 0;
    var direction = 0;

    const header = document.querySelector('.header');
    // const s = document.querySelector('.sss');
    const timerId = {
      id: null
    };
    window.addEventListener('scroll', function() {
      let scrolled_y = window.pageYOffset;
      direction = scrolled_y > scrol_fix ? 1 : 0;

      if (direction) {
        if (window.scrollY > 20 && timerId.id === null) {
          timerId.id = setTimeout(function() {
            header.classList.add('header_fix');
            // s.classList.add('_fix');
            timerId.id = null;
          }, 50);
        }
      } else {
        if (window.scrollY <= 10 && header.classList.contains('header_fix')) {
          header.classList.remove('header_fix');
          // s.classList.remove('_fix');
          if (timerId.id !== null) {
            clearTimeout(timerId.id);
            timerId.id = null;
          };
        }
      }
      scrol_fix = scrolled_y;
    });


    // burger
    let menuWrapper = document.querySelector('.bur__wrapper'); 
    let burger = document.querySelector('#burger-btn');
    let burgerWrapper = document.querySelector('.bur');
    let headerWrapper = document.querySelector('.header__wrapper');
    let body = document.querySelector('body');
    let headerMain = document.querySelector('.header');
    burger.addEventListener('click', () => {
        console.log(headerWrapper);
        burger.classList.toggle('active');
        menuWrapper.classList.toggle('visibility');
        burgerWrapper.classList.toggle('show');
        headerWrapper.classList.toggle('hidden');
        body.classList.toggle('no-scroll');
        headerMain.classList.toggle('burger__open');
        burgerWrapper.classList.toggle('visibility');
        setTimeout(() => {
        },400)
    })
      
      

    // animation fullsreen
    let cont = document.querySelector('.animate-container');
    for(let i = 1; i <= 100; i++){
        let dot = document.createElement('div');
        dot.classList.add('element');
        cont.appendChild(dot);
    }
    let dotAll = document.querySelectorAll('.element');
    let animation = anime.timeline({
        targets: dotAll,
        easing: 'easeInOutExpo',
        loop: true,
        delay: anime.stagger(40,{grid:[10, 10] , from: 'center'}),
    })
    animation
    .add({
        rotateZ: 180,
        translateY: anime.stagger(-60,{ grid:[10,10], from: 'center', axis:'y'}),
        translateX: anime.stagger(-60,{ grid:[10,10], from: 'center', axis:'x'}),
    })
    .add({
        rotateZ: 180,
        translateY: anime.stagger(0,{ grid:[10,10], from: 'center', axis:'y'}),
        translateX: anime.stagger(0,{ grid:[10,10], from: 'center', axis:'x'}),
        opacite: 1,
    })
    


    // parallax 3d
    var c5 = new Rellax('.animate-container',{
        speed: -2
    })


    // click button
    let allBtns = document.querySelectorAll('.button-click');
    allBtns.forEach(el => {
        el.addEventListener('click', function(e){
            e.preventDefault()
            let x = e.clientX - e.target.offsetLeft;
            let y = e.clientY - e.target.offsetTop;
            let riples = document.createElement('i');
            riples.style.left = x + 'px';
            riples.style.top = y + 'px';
            this.appendChild(riples);


            setTimeout(() => {
                riples.remove()
            },1200)
        })
    })


    

})();